<template>
    <thead class = "thead-dark">
        <!--<th v-for="(item, key) in camposaMostrar" v-bind:property= "item" v-bind:key="key">{{item.title}}<th>-->
        <Header v-for="(item, key) in camposaMostrar" :property= "item" :key="key" />
        <th scope = "col">Acciones</th>
    </thead>
</template>

<script>
import { mapState } from "vuex";
import Header from "@/components/Header.vue"

export default {
  name: "table-headers",
  components:{
      Header
  },
  data () {
    return {
    }
  },
  props:[
      "schemaName"
  ],
  computed: {
    ...mapState(["datos", "schema"]),
    properties(){
      //alert("schemaName: " + JSON.stringify(this.schemaName));
      return this.schema.definitions[this.schemaName].properties;
    },
    camposaMostrar(){
      let campos = {};
      for(var i in this.properties){
        if(this.properties[i].mostrarenTabla){
          campos[i] = this.properties[i];
        }
      }
      //alert("campos a mostrar: " + JSON.stringify(campos));
      return campos
    }
  },
  methods:{
    
  }
}
</script>