<template>
    <tr scope = "row">
        <Cell v-for="(dato, i) of camposaMostrar"  :key = "i"  :collectionName = "collectionName" :campoaMostrar = "dato" :item = "item" :property="i" />
        <td class = "text-center">
            <button v-on:click="agregar(item)">
                <i class="fa fa-plus text-primary"></i>
            </button>
        </td>
    </tr>
</template>

<script>
// @ is an alias to /src
import Vue from "vue"
import { mapState } from "vuex";
import Cell from "@/components/Cell.vue";
export default {
  name: "RowAddMessage",
  components:{
      Cell
  },
  data () {
    return {
      
    }
  },
  props:[
      "item",
      "collectionName",
      "index",
      "camposaMostrar"
  ],
  computed: {
    ...mapState(["params", "datos", "schema"])
  },
  methods:{
    editar(item, index){
      /*this.params.indexenEdicion = index;
      this.params.registroNuevo = false;
      this.params.enEdicion = true;
      this.params.registroenEdicion = item;*/
      let vm = this;
      vm.$emit('editar', item, index)
    },
    eliminar(index){
      this.params.datos.splice(index, 1);
      
    },
    agregar(item){
      //alert("agregando Mensaje");
      this.params.registroenEdicion.messages.push({idMessage:item.idMessage});
    }
  }
}
</script>