<template>
    <div>
        <div style = "" class = "table-channelPautas" v-show = "params.enEdicion">
            <div style = "" class = "overflow-auto">
                <table class = "table table-bordered table-hover table-striped table-sm">
                    <thead class="thead-dark">
                        <th scope="col">Id</th>
                        <th scope="col">Nombre</th>
                        <th scope="col">Fuente</th>
                        <th scope="col">Fecha de inicio</th>
                        <th scope="col">Hora de inicio</th>
                        <th scope="col">Fecha final</th>
                        <th scope="col">Hora final</th>
                        <th scope="col">Intervalo de reproduccion</th>
                        <th scope="col">Interrumpir eventos</th>
                        <th scope="col">Panel</th>
                        <th scope="col">Acciones</th>
                    </thead>
                    <tbody>
                        <RowMinus v-for="(item, index) of pautasdeCanal" :camposaMostrar= "camposaMostrar" :collectionName = "schema.definitions.pauta" :key="index" :item = "item" :index = "index" v-on:editar = "editar"/>
                    </tbody>
                </table>
                <div class = "row"></div>
                <div  class = "text-left">
                    Pautas disponibles:
                </div>
                <div>
                    <table class = "table table-bordered table-hover table-striped table-sm">
                    <thead class="thead-dark">
                        <th scope="col">Id</th>
                        <th scope="col">Nombre</th>
                        <th scope="col">Fuente</th>
                        <th scope="col">Fecha de inicio</th>
                        <th scope="col">Hora de inicio</th>
                        <th scope="col">Fecha final</th>
                        <th scope="col">Hora final</th>
                        <th scope="col">Intervalo de reproduccion</th>
                        <th scope="col">Interrumpir eventos</th>
                        <th scope="col">Panel</th>
                        <th scope="col">Acciones</th>
                    </thead>
                    <tbody>
                        <template v-if = "params.registroenEdicion">
                            <RowAdd v-for="(item, index) of pautasaSeleccionar" :camposaMostrar= "camposaMostrar" :collectionName = "schema.definitions.pauta" :key="index" :item = "item" :index = "index" v-on:editar = "editar"/>
                        </template>
                    </tbody>
                </table>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import { mapState } from "vuex";

import Vue from 'vue'
import Row from "@/components/Row.vue"
import RowAdd from "@/components/RowAdd.vue"
import RowMinus from "@/components/RowMinus.vue"

export default {
    name: "ChannelPautas",
    components:{
        Row,
        RowAdd,
        RowMinus
    },
    
    data () {
        return {
        }
    },
    props:[
      "item"
    ],
    computed: {
        ...mapState(["params", "datos", "schema"]),
        pautasdeCanal(){
            let pautas = [];
            if(this.params.registroenEdicion){
                for(let i = 0; i < this.params.registroenEdicion.pautas.length; i++){
                    for(let j = 0; j < this.datos.pautas.length; j++ ){
                        if(this.params.registroenEdicion.pautas[i].idPauta == this.datos.pautas[j].idPauta){
                            pautas.push(this.datos.pautas[j]);
                        }
                    }
                }
            }
            /*alert("this.params.registroenEdicion.pautas: " + JSON.stringify(this.params.registroenEdicion.pautas));
            alert("this.datos.pautas: " + JSON.stringify(this.datos.pautas));
            alert("pautas de Canal: " + JSON.stringify(pautas));*/
            return pautas;
        },
        pautasaSeleccionar(){
            let pautas = [];
            if(this.params.registroenEdicion){
                for(let i = 0; i < this.datos.pautas.length ; i++){
                    var pautaSeleccionada = false;
                    for(let j = 0; j < this.params.registroenEdicion.pautas.length; j++ ){
                        if(this.params.registroenEdicion.pautas[j].idPauta == this.datos.pautas[i].idPauta){
                            pautaSeleccionada = true;
                            break;
                        }
                    }
                    if(!pautaSeleccionada){
                        pautas.push(this.datos.pautas[i]);
                    }
                }
            }
            /*alert("this.params.registroenEdicion.pautas: " + JSON.stringify(this.params.registroenEdicion.pautas));
            alert("this.datos.pautas: " + JSON.stringify(this.datos.pautas));
            alert("pautas a seleccionar: " + JSON.stringify(pautas));*/
            return pautas;
        },
        properties(){
            //alert("schemaName: " + JSON.stringify(this.schemaName));
            return this.schema.definitions.pauta.properties;
        },
        camposaMostrar(){
            let campos = {};
            for(var i in this.properties){
                if(this.properties[i].mostrarenTabla){
                    campos[i] = this.properties[i];
                }
            }
            //alert("campos a mostrar: " + JSON.stringify(campos));
            return campos
        }
    },
    mounted(){
        //alert("en channelPautas: " + JSON.stringify(this.item));
        $('.carousel').carousel({
          interval: 2000
        })
    },
    updated(){
        //alert("en channelPautas: " + JSON.stringify(this.item));
        $('.carousel').carousel({
          interval: 2000
        })
    },
    methods:{
        editar(){
        },
        agregarPauta(){
            //alert("agregando pauta");
        }
    }
}
</script>