<template>
    <div>
        <div style = "" class = "table-channelSources" v-show = "params.enEdicion">
            <div style = "" class = "overflow-auto">
                <table class = "table table-bordered table-hover table-striped table-sm">
                    <thead class="thead-dark">
                        <th scope="col">Id</th>
                        <th scope="col">URI</th>
                        <th scope="col">Nombre</th>
                        <th scope="col">Tipo</th>
                        <th scope="col">Estado</th>
                        <th scope="col">Acciones</th>
                    </thead>
                    <tbody>
                        <template v-if = "params.registroenEdicion">
                            <RowMinusSource v-for="(item, index) of sourcesdeSequence" :camposaMostrar= "camposaMostrar" :collectionName = "schema.definitions.Source" :key="index" :item = "item" :index = "index" v-on:editar = "editar"/>
                        </template>
                    </tbody>
                </table>
                <div class = "row"></div>
                <div  class = "text-left">
                    Sources disponibles:
                </div>
                <div>
                    <table class = "table table-bordered table-hover table-striped table-sm">
                    <thead class="thead-dark">
                        <th scope="col">Id</th>
                        <th scope="col">URI</th>
                        <th scope="col">Nombre</th>
                        <th scope="col">Tipo</th>
                        <th scope="col">Estado</th>
                        <th scope="col">Acciones</th>
                    </thead>
                    <tbody>
                        <RowAddSource v-for="(item, index) of sourcesaSeleccionar" :camposaMostrar= "camposaMostrar" :collectionName = "schema.definitions.Source" :key="index" :item = "item" :index = "index" v-on:editar = "editar"/>
                    </tbody>
                </table>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import { mapState } from "vuex";

import Vue from 'vue'
import Row from "@/components/Row.vue"
import RowAddSource from "@/components/RowAddSource.vue"
import RowMinusSource from "@/components/RowMinusSource.vue"

export default {
    name: "SequenceSources",
    components:{
        Row,
        RowAddSource,
        RowMinusSource
    },
    
    data () {
        return {
        }
    },
    props:[
      "item"
    ],
    computed: {
        ...mapState(["params", "datos", "schema"]),
        sourcesdeSequence(){
            let sources = [];
            //alert("this.params.registroenEdicion.sources: " + JSON.stringify(this.params.registroenEdicion.sources));
            //alert("this.datos.sources: " + JSON.stringify(this.datos.sources));
            for(let i = 0; i < this.params.registroenEdicion.sources.length; i++){
                for(let j = 0; j < this.datos.sources.length; j++ ){
                    if(this.params.registroenEdicion.sources[i].idSource == this.datos.sources[j].idSource){
                        sources.push(this.datos.sources[j]);
                    }
                }
            }
            //alert("Sources de Canal: " + JSON.stringify(sources));
            return sources;
        },
        sourcesaSeleccionar(){
            let sources = [];
            for(let i = 0; i < this.datos.sources.length ; i++){
                var sourceSeleccionado = false;
                for(let j = 0; j < this.params.registroenEdicion.sources.length; j++ ){
                    if(this.params.registroenEdicion.sources[j].idSource == this.datos.sources[i].idSource){
                        sourceSeleccionado = true;
                        break;
                    }
                }
                if(!sourceSeleccionado){
                    sources.push(this.datos.sources[i]);
                }
            }
            /*alert("this.params.registroenEdicion.pautas: " + JSON.stringify(this.params.registroenEdicion.pautas));
            alert("this.datos.pautas: " + JSON.stringify(this.datos.pautas));
            alert("pautas a seleccionar: " + JSON.stringify(pautas));*/
            return sources;
        },
        properties(){
            //alert("schemaName: " + JSON.stringify(this.schemaName));
            return this.schema.definitions.source.properties;
        },
        camposaMostrar(){
            let campos = {};
            for(var i in this.properties){
                if(this.properties[i].mostrarenTabla){
                    campos[i] = this.properties[i];
                }
            }
            //alert("campos a mostrar: " + JSON.stringify(campos));
            return campos
        }
    },
    mounted(){
        //alert("en channelPautas: " + JSON.stringify(this.item));
        $('.carousel').carousel({
          interval: 2000
        })
    },
    updated(){
        //alert("en channelPautas: " + JSON.stringify(this.item));
        $('.carousel').carousel({
          interval: 2000
        })
    },
    methods:{
        editar(){
        },
        agregarSource(){
            //alert("agregando mensaje");
        }
    }
}
</script>