<template>
    <tr scope = "row">
        <Cell v-for="(dato, i) of camposaMostrar"  :key = "i"  :collectionName = "collectionName" :campoaMostrar = "dato" :item = "item" :property="i" />
        <td>
            <button @click="editar(item, index)">
                <i class="fa fa-pencil text-primary"></i>
            </button>
            <button v-on:click="eliminar(index)">
                <i class="fa fa-times text-danger"></i>
            </button>
        </td>
    </tr>
</template>

<script>
// @ is an alias to /src
import Vue from "vue"
import { mapState } from "vuex";
import Cell from "@/components/Cell.vue";
export default {
  name: "row",
  components:{
      Cell
  },
  data () {
    return {
      
    }
  },
  props:[
      "item",
      "collectionName",
      "index",
      "camposaMostrar"
  ],
  computed: {
    ...mapState(["params", "datos", "schema"])
  },
  methods:{
    editar(item, index){
      /*this.params.indexenEdicion = index;
      this.params.registroNuevo = false;
      this.params.enEdicion = true;
      this.params.registroenEdicion = item;*/
      let vm = this;
      vm.$emit('editar', item, index)
    },
    eliminar(item, index){
      //this.params.datos[colectionName].splice(index, 1);
      let vm = this;
      vm.$emit('eliminar', item, index)
    },
  }
}
</script>