<template>
    <tbody>
        <Row v-for="(item, index) of datos" :camposaMostrar= "camposaMostrar" :collectionName = "collectionName" :key="index" :item = "item" :index = "index" v-on:editar = "editar" v-on:eliminar = "eliminar"/>
    </tbody>
</template>

<script>
// @ is an alias to /src

import { mapState } from "vuex";
import Row from "@/components/Row.vue";


export default {
  name: "table-body",
  components:{
      Row
  },
  data () {
    return {
      
    }
  },
  props:[
      "schemaName",
      "collectionName",
      "datos"
  ],
  computed: {
    ...mapState(["schema"]),
    properties(){
      return this.schema.definitions[this.schemaName].properties;
    },
    camposaMostrar(){
      let campos = {};
      for(var i in this.properties){
        if(this.properties[i].mostrarenTabla){
          campos[i] = this.properties[i];
        }
      }
      //alert("campos a mostrar: " + JSON.stringify(campos));
      return campos
    },
    datosaMostrar(){
      let campos = this.camposaMostrar;
      let datosaMostrar = [];
      for(var item of this.datos){
        let dat = {};
        for(var i in campos){
          //alert(i + ": " + JSON.stringify(campos[i]));
          dat[i] = item[i];
        }
        datosaMostrar.push(dat);
      }
      //alert("datos a mostrar: " + JSON.stringify(datosaMostrar));
      return datosaMostrar
    }
  },
  methods:{
    editar(item, index){
      let vm = this;
      vm.$emit('editar', item, index)

    },
    eliminar(item, index){
      this.datos.splice(index, 1);
      
    }
  }
}
</script>