<template>
    <tr scope = "row">
        <Cell v-for="(dato, i) of camposaMostrar"  :key = "i"  :collectionName = "collectionName" :campoaMostrar = "dato" :item = "item" :property="i" />
        <td class = "text-center">
            <button v-on:click="eliminar(item)">
                <i class="fa fa-minus text-danger"></i>
            </button>
        </td>
    </tr>
</template>

<script>
// @ is an alias to /src
import Vue from "vue"
import { mapState } from "vuex";
import Cell from "@/components/Cell.vue";
export default {
  name: "RowMinusMessage",
  components:{
      Cell
  },
  data () {
    return {
      
    }
  },
  props:[
      "item",
      "collectionName",
      "index",
      "camposaMostrar"
  ],
  computed: {
    ...mapState(["params", "datos", "schema"])
  },
  methods:{
    editar(item, index){
      /*this.params.indexenEdicion = index;
      this.params.registroNuevo = false;
      this.params.enEdicion = true;
      this.params.registroenEdicion = item;*/
      let vm = this;
      vm.$emit('editar', item, index)
    },
    eliminar(item){
      //alert("Eliminando Mensaje");
      for(let i = 0; i < this.params.registroenEdicion.messages.length; i++){
        if(this.params.registroenEdicion.messages[i].idMessage == item.idMessage){
          this.params.registroenEdicion.messages.splice(i, 1);
          break;
        }
      }
      
    },
    agregar(item){
      this.params.registroenEdicion.messages.push({idMessage:item.idMessage});
    }
  }
}
</script>