<template>
    <div>
        <div style = "" class = "table-channelMessages" v-show = "params.enEdicion">
            <div style = "" class = "overflow-auto">
                <table class = "table table-bordered table-hover table-striped table-sm">
                    <thead class="thead-dark">
                        <th scope="col">Id</th>
                        <th scope="col">Nombre</th>
                        <th scope="col">Texto</th>
                        <th scope="col">Fecha de inicio</th>
                        <th scope="col">Hora de inicio</th>
                        <th scope="col">Fecha final</th>
                        <th scope="col">Hora final</th>
                        <th scope="col">Panel</th>
                        <th scope="col">Rotar</th>
                        <th scope="col">URI</th>
                        <th scope="col">Acciones</th>
                    </thead>
                    <tbody>
                        <template v-if = "params.registroenEdicion">
                            <RowMinusMessage v-for="(item, index) of MessagesdeCanal" :camposaMostrar= "camposaMostrar" :collectionName = "schema.definitions.Message" :key="index" :item = "item" :index = "index" v-on:editar = "editar"/>
                        </template>
                    </tbody>
                </table>
                <div class = "row"></div>
                <div  class = "text-left">
                    Messages disponibles:
                </div>
                <div>
                    <table class = "table table-bordered table-hover table-striped table-sm">
                    <thead class="thead-dark">
                        <th scope="col">Id</th>
                        <th scope="col">Nombre</th>
                        <th scope="col">Texto</th>
                        <th scope="col">Fecha de inicio</th>
                        <th scope="col">Hora de inicio</th>
                        <th scope="col">Fecha final</th>
                        <th scope="col">Hora final</th>
                        <th scope="col">Panel</th>
                        <th scope="col">Rotar</th>
                        <th scope="col">URI</th>
                        <th scope="col">Acciones</th>
                    </thead>
                    <tbody>
                        <RowAddMessage v-for="(item, index) of messagesaSeleccionar" :camposaMostrar= "camposaMostrar" :collectionName = "schema.definitions.Message" :key="index" :item = "item" :index = "index" v-on:editar = "editar"/>
                    </tbody>
                </table>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import { mapState } from "vuex";

import Vue from 'vue'
import Row from "@/components/Row.vue"
import RowAddMessage from "@/components/RowAddMessage.vue"
import RowMinusMessage from "@/components/RowMinusMessage.vue"

export default {
    name: "ChannelMessages",
    components:{
        Row,
        RowAddMessage,
        RowMinusMessage
    },
    
    data () {
        return {
        }
    },
    props:[
      "item"
    ],
    computed: {
        ...mapState(["params", "datos", "schema"]),
        MessagesdeCanal(){
            let messages = [];
            //alert("this.params.registroenEdicion.messages: " + JSON.stringify(this.params.registroenEdicion.messages));
            //alert("this.datos.messages: " + JSON.stringify(this.datos.messages));
            if(this.params.registroenEdicion){
                for(let i = 0; i < this.params.registroenEdicion.messages.length; i++){
                    for(let j = 0; j < this.datos.messages.length; j++ ){
                        if(this.params.registroenEdicion.messages[i].idMessage == this.datos.messages[j].idMessage){
                            messages.push(this.datos.messages[j]);
                        }
                    }
                }
            }
            //alert("Mensajes de Canal: " + JSON.stringify(messages));
            return messages;
        },
        messagesaSeleccionar(){
            let messages = [];
            for(let i = 0; i < this.datos.messages.length ; i++){
                var messageSeleccionado = false;
                for(let j = 0; j < this.params.registroenEdicion.messages.length; j++ ){
                    if(this.params.registroenEdicion.messages[j].idMessage == this.datos.messages[i].idMessage){
                        messageSeleccionado = true;
                        break;
                    }
                }
                if(!messageSeleccionado){
                    messages.push(this.datos.messages[i]);
                }
            }
            /*alert("this.params.registroenEdicion.pautas: " + JSON.stringify(this.params.registroenEdicion.pautas));
            alert("this.datos.pautas: " + JSON.stringify(this.datos.pautas));
            alert("pautas a seleccionar: " + JSON.stringify(pautas));*/
            return messages;
        },
        properties(){
            //alert("schemaName: " + JSON.stringify(this.schemaName));
            return this.schema.definitions.message.properties;
        },
        camposaMostrar(){
            let campos = {};
            for(var i in this.properties){
                if(this.properties[i].mostrarenTabla){
                    campos[i] = this.properties[i];
                }
            }
            //alert("campos a mostrar: " + JSON.stringify(campos));
            return campos
        }
    },
    mounted(){
        //alert("en channelPautas: " + JSON.stringify(this.item));
        $('.carousel').carousel({
          interval: 2000
        })
    },
    updated(){
        //alert("en channelPautas: " + JSON.stringify(this.item));
        $('.carousel').carousel({
          interval: 2000
        })
    },
    methods:{
        editar(){
        },
        agregarMessage(){
            //alert("agregando mensaje");
        }
    }
}
</script>