<template>
    <td v-if = "campoaMostrar.type == 'boolean'">
        <template v-if = "item[property] == 'true'" >SI</template>
        <template v-else = "" >NO</template>
    </td>
    <td v-else-if = "campoaMostrar.type == 'color'" align = "center" style = "display: flex; align-items: center; justify-content: center; margin: 0px; text-align:center;">
      <div class = "circulo" v-bind:style = "{backgroundColor: item[property]}">&nbsp;</div>
    </td>
    <td v-else-if = "campoaMostrar.reference">
        {{datodeReferencia}}
    </td>
    <td v-else = "">{{item[property]}}</td>
</template>

<script>
// @ is an alias to /src

import { mapState } from "vuex";
import Vue from 'vue'


export default {
  name: "cell",
  data () {
    return {
     
    }
  },
  props:[
      "property",
      "collectionName",
      "item", 
      "campoaMostrar"
  ],
  computed: {
    ...mapState(["datos", "schema"]),
    datodeReferencia(){
      //alert("en datos de referenca");
      let datodeReferencia = "";
      let datos1 = this.datos[this.campoaMostrar.reference];
      //alert("this.schema.properties[this.campoaMostrar.reference].items$ref: " + JSON.stringify(this.schema.properties[this.campoaMostrar.reference].items.$ref.substring(1)));
      let schemaReference = this.schema.properties[this.campoaMostrar.reference].items.$ref.substring(1);
      let identificador = this.schema.definitions[schemaReference].id;
      let name = this.schema.definitions[schemaReference].name;
      //alert("identificador: " + identificador + ", name: " + name)
      for(var i = 0; i < datos1.length; i++){
        if(datos1[i][identificador] == this.item[this.property]){
          datodeReferencia = datos1[i][name];
          break;
        }
      }
      //alert("datos de referencia: " + JSON.stringify(datosdeReferencia));
      //alert("campos de referencia: " + JSON.stringify(camposdeReferencia));
      //alert("identificador: " + identificador);
      //alert("name: " + name);*/
      return datodeReferencia;
    }
  },
 
  methods:{
    
  },
  mounted(){
    //alert("campoaMostrar: " + JSON.stringify( this.campoaMostrar));
  },
  components: { 

  }
}
</script>

<style scoped>
.circulo {
     width: 20px;
     height: 20px;
     -moz-border-radius: 50%;
     -webkit-border-radius: 50%;
     border-radius: 50%;
     background: #5cb85c;
}
</style>