<template>
  <div class = "container text-center">
    <div style = "" class = "table-messages" v-show = "!params.enEdicion">
      <div style = "" class = "overflow-auto">
        <table class = "table table-bordered table-hover table-striped table-sm">
          <TableHeaders v-bind:schemaName= "schemaName"/>
          <TableBody v-bind:datos = "instancia" v-bind:schemaName = "schemaName" v-bind:collectionName = "collectionName" v-on:editar = "editar"/>
        </table>
      </div>
      <div class = "text-left">
        <button class = "btn btn-primary " v-on:click="agregar">Agregar</button>
      </div>
    </div>
    <div class = "form-messages text-left"  v-show = "params.enEdicion">
      <ul class="nav nav-pills mb-3" id="pills-tab" role="tablist">
        <li class="nav-item"  v-if = "!this.params.registroNuevo">
          <a class="nav-link active" id="pills-home-tab" data-toggle="pill" href="#datos" role="tab" aria-controls="pills-home" aria-selected="true">Datos</a>
        </li>
        <li class="nav-item" v-if = "!this.params.registroNuevo">
          <a class="nav-link" id="pills-profile-tab" data-toggle="pill" href="#pautas" role="tab" aria-controls="pills-profile" aria-selected="false">Pautas</a>
        </li>
        <li class="nav-item" v-if = "!this.params.registroNuevo">
          <a class="nav-link" id="pills-contact-tab" data-toggle="pill" href="#eventos" role="tab" aria-controls="pills-contact" aria-selected="false">Eventos</a>
        </li>
        <li class="nav-item" v-if = "!this.params.registroNuevo">
          <a class="nav-link" id="pills-contact-tab" data-toggle="pill" href="#mensajes" role="tab" aria-controls="pills-contact" aria-selected="false">Mensajes</a>
        </li>
      </ul>
      <div class="tab-content" id="myTabContent">
        <div class="tab-pane fade show active" id="datos" role="tabpanel" aria-labelledby="home-tab">
          <template v-if = "params.registroenEdicion">
            <form action="" class = "">
              <div v-for="(item, index) of camposaEditar"  :key="index" class = "row">
                <div v-for="(item1, key) in item" :key = "key" v-bind:class = "'form-group col-sm-' + item1.cols">
                  <label v-bind:for = "key" class = "col-form-label text-right">{{item1.title}}:</label>
                  <template v-if = "item1.type == 'string'">
                    <template v-if = "item1.enum != undefined">
                      <select class="form-control" :id="key">
                        <option v-for= "(opcion, index) of item1.enum" :key = "index" :value = "opcion">{{opcion}}</option>
                      </select>
                    </template>
                    <template v-else = "">
                      <input class = "form-control form-control-sm" v-bind:id = "key" type="text" name="key" v-bind:placeholder="item1.title" :readonly = "properties[key].readonly" v-model="params.registroenEdicion[key]">
                    </template>
                  </template>
                  <template v-else-if = "item1.type == 'boolean'">
                    <select class="form-control" :id="key" v-model="params.registroenEdicion[key]">
                      <option value = "true">SI</option>
                      <option value = "false">NO</option>
                    </select>
                  </template>
                  <template v-else-if = "item1.type == 'integer'">
                    <template v-if = "item1.reference">
                      <select class="form-control" :id="key" v-model="params.registroenEdicion[key]">
                        <option v-for = "item2 in datosdeReferencia(item1.reference)" :key= "item2.id" :value = "item2.id">{{item2.text}}</option>
                      </select>
                    </template>
                    <template v-else = "">
                      <input class = "form-control form-control-sm" v-bind:id = "key" type="text" name="key" v-bind:placeholder="item1.title" :readonly = "properties[key].readonly" v-model="params.registroenEdicion[key]">
                    </template>
                  </template>
                  <template v-else-if = "item1.type == 'color'">
                    <ChromePicker v-model = "params.registroenEdicion[key]"/>
                  </template>
                  <template v-else-if = "item1.type == 'date'">
                    <input class = "form-control form-control-sm" v-bind:id = "key" type="date" :name="key" v-bind:placeholder="item1.title" :readonly = "properties[key].readonly" v-model="params.registroenEdicion[key]" >
                  </template>
                  <template v-else-if = "item1.type == 'time'">
                    <input class = "form-control form-control-sm" v-bind:id = "key" type="time" :name="key" v-bind:placeholder="item1.title" :readonly = "properties[key].readonly" v-model="params.registroenEdicion[key]">
                  </template>
                  <template v-else = "">
                    <input class = "form-control form-control-sm" v-bind:id = "key" type="text" :name="key" v-bind:placeholder="item1.title" :readonly = "properties[key].readonly" v-model="params.registroenEdicion[key]">
                  </template>
                </div>
              </div> 
            </form>
          </template>
        </div>
        <div class="tab-pane fade" id="pautas" role="tabpanel" aria-labelledby="contact-tab">
          <ChannelPautas v-if = "params.registroenEdicion.pautas" :item = "params.registroenEdicion"></ChannelPautas>
        </div>
        <div class="tab-pane fade" id="eventos" role="tabpanel" aria-labelledby="contact-tab">
          <ChannelEvents v-if = "params.registroenEdicion.events" :item = "params.registroenEdicion"></ChannelEvents>
        </div>
        <div class="tab-pane fade" id="mensajes" role="tabpanel" aria-labelledby="contact-tab">
          <ChannelMessages v-if = "params.registroenEdicion.messages" :item = "params.registroenEdicion"></ChannelMessages>
        </div>
      </div>
      <div v-if = "params.registroNuevo" class = "text-center">
        <button class = "btn btn-primary " v-on:click="registrar">Registrar</button>
      </div>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src

import { mapState } from "vuex";
import TableHeaders from "@/components/TableHeaders.vue";
import TableBody from "@/components/TableBody.vue";


import ChromePicker from "@/components/ChromePicker.vue";
import ChannelPautas from "@/components/ChannelPautas.vue";
import ChannelEvents from "@/components/ChannelEvents.vue";
import ChannelMessages from "@/components/ChannelMessages.vue";

import { Photoshop } from 'vue-color'





export default {
  name: "CollectionChannel",
  components:{
    TableHeaders,
    TableBody,
 
    'photoshop-picker': Photoshop,
    ChromePicker,
    ChannelPautas,
    ChannelEvents,
    ChannelMessages
  },
    
  data () {
    return {
      color: "#ffffff"
    }
  },
  props:[
    "schemaName",
    "collectionName",
    "instancia"
  ],
  computed: {
    ...mapState(["params", "datos", "schema"]),
    properties(){
      return this.schema.definitions[this.schemaName].properties;
    },
    pautasaMostrar(){
      let pautas = [];
      //alert("this.params.registroenEdicion: " + JSON.stringify(this.params.registroenEdicion));
      if(this.params.registroenEdicion != null){
        /*for(var i = 0; i < this.params.registroenEdicion.pautas.length; i++){
          for(var j = 0; j < this.datos.pautas.length; j++){
            if(this.params.registroenEdicion.pautas[i].idPauta == this.datos.pautas[j].idPauta){
              pautas.add(this.params.registroenEdicion.pautas[i]);
            }
          }  
        }*/
      }
      return pautas;
    },
    camposaMostrar(){
      let campos = {};
      for(var i in this.properties){
        if(this.properties[i].mostrarenTabla){
          campos[i] = this.properties[i];
        }
      }
      //alert("campos a mostrar: " + JSON.stringify(campos));
      return campos
    },
    camposaEditar(){
      let campos = [];
      let row = 0;
      for(var i in this.properties){
        if(this.properties[i].type != "array" && this.properties[i].type != "object"){
          if(!campos[this.properties[i].row]){
           campos[this.properties[i].row] = {};
          }
          campos[this.properties[i].row][i] = this.properties[i];
        }
      }
      return campos
    },
    datosaMostrar(){
      let campos = this.camposaMostrar;
      let datosaMostrar = [];
      for(var item of this.data){
        let dat = {};
        for(var i in campos){
          //alert(i + ": " + JSON.stringify(campos[i]));
          dat[i] = item[i];
        }
        datosaMostrar.push(dat);
      }
      //alert("datos a mostrar: " + JSON.stringify(datosaMostrar));
      return datosaMostrar
    },
    registroNuevo(){
      let item = {};
      for(var i in this.properties){
        item[i] = this.properties[i].default;
      }
      return item; 
    }
  },
  methods:{
    datosdeReferencia(referencia){
      //alert("referencia: "+ JSON.stringify(referencia));
      let datosdeReferencia = [];
      let datos1 = this.datos[referencia];
      let schemaReferenciado = this.schema.properties[referencia].items.$ref.substring(1);
      schemaReferenciado = this.schema.definitions[schemaReferenciado];
      //alert("schemaReferenciado: " + JSON.stringify(schemaReferenciado));
      let identificador = schemaReferenciado.id;
      let name = schemaReferenciado.name;
      for(let i = 0; i < datos1.length; i++){
        datosdeReferencia.push({id:datos1[i][identificador], text: datos1[i][name]});
      }
      //alert("datos de referencia: " + JSON.stringify(datosdeReferencia));
      //alert("campos de referencia: " + JSON.stringify(camposdeReferencia));
      //alert("identificador: " + identificador);
      //alert("name: " + name);
     return datosdeReferencia;

    },
    
    eliminar(index){
      this.instancia.splice(index, 1);
      
    },
    agregar(){
      this.params.registroNuevo = true;
      this.params.enEdicion = true;
      let item = {};
      for(var i in this.properties){
        item[i] = this.properties[i].default;
      }
      this.params.registroenEdicion = item; 
    },
    validar(){
      var errores = "";
      /*if(this.registroenEdicion.x == ""){
        if(errores != ""){
          errores += "\n";
        }
        errores += "- No ha indicado la posición X del recuadro";
      }
      if(this.registroenEdicion.y == ""){
        if(errores != ""){
          errores += "\n";
        }
        errores += "- No ha indicado la posición Y de recuadro";
      }
      if(this.registroenEdicion.width == ""){
        if(errores != ""){
          errores += "\n";
        }
        errores += "- No ha indicado el Ancho del recuadro";
      }
      if(this.registroenEdicion.height == ""){
        if(errores != ""){
          errores += "\n";
        }
        errores += "- No ha indicado el Alto del recuadro";
      }
      if(this.registroenEdicion.border == ""){
        if(errores != ""){
          errores += "\n";
        }
        errores += "- No ha indicado el borde del recuadro";
      }*/
      return errores;
    },
    registrar(){
      if(this.params.registroNuevo){
        let mensajesdeError = this.validar();
        if(mensajesdeError == ""){
          let maxId = 0;
          for(let item of this.instancia){
            if(item[this.schema.definitions[this.schemaName].id] > maxId){
              maxId = item[this.schema.definitions[this.schemaName].id];
            }
          }
          maxId ++;
          this.params.registroenEdicion[this.schema.definitions[this.schemaName].id] = maxId;
          this.instancia.push(this.params.registroenEdicion);
          this.params.registroNuevo = false;
          this.params.enEdicion = false;
        }else{
          alert(mensajesdeError);
        }
      }else{
        this.params.enEdicion = false;
      }
    },
    editar(item, index){
      //alert("editando en CollectionChannel item: " + item + ", index: " + index);
      
      //alert("editando en Collection item: " + item + ", index: " + index);
      //alert("index en edicion " + this.registroenEdicion);
      //alert("editando " + index);
      //alert("editando " + JSON.stringify(item));
      this.params.indexenEdicion = index;
      this.params.registroNuevo = false;
      this.params.enEdicion = true;
      this.params.registroenEdicion = item;
      //store.commit("editarEvent", item.id);
    }
  },
  created(){

    //alert("schema: " + JSON.stringify(this.schema));  
    this.params.datos = this.instancia;
    this.params.schema = this.schema.definitions[this.schemaName];
      
  },
  mounted(){
    //alert("En Collection, schemaName: " + this.schemaName);
    //alert("En Collection, collectionName: " + this.collectionName);
    //alert("schema: " + JSON.stringify(this.schema.definitions[this.schemaName]));
    //alert("datos: " + JSON.stringify(this.datos));
    this.params.registroNuevo = false;
    this.params.enEdicion = false;
    this.params.registroenEdicion = {};
    this.params.indexenEdicion = -1;
    this.params.schema = null;
    this.params.datos = null;
      
      
  },
  beforeCreated(){
  }
}
</script>