<template>
    <div>
        <div style = "" class = "table-channelEvents" v-show = "params.enEdicion">
            <div style = "" class = "overflow-auto">
                <table class = "table table-bordered table-hover table-striped table-sm">
                    <thead class="thead-dark">
                        <th scope="col">Id</th>
                        <th scope="col">Nombre</th>
                        <th scope="col">Fuente</th>
                        <th scope="col">Acciones</th>
                    </thead>
                    <tbody>
                        <RowMinusEvent v-for="(item, index) of eventosdeCanal" :camposaMostrar= "camposaMostrar" :collectionName = "schema.definitions.event" :key="index" :item = "item" :index = "index" v-on:editar = "editar"/>
                    </tbody>
                </table>
                <div class = "row"></div>
                <div  class = "text-left">
                    Eventos disponibles:
                </div>
                <div>
                    <table class = "table table-bordered table-hover table-striped table-sm">
                    <thead class="thead-dark">
                        <th scope="col">Id</th>
                        <th scope="col">Nombre</th>
                        <th scope="col">Fuente</th>
                        <th scope="col">Acciones</th>
                    </thead>
                    <tbody>
                        <template v-if = "params.registroenEdicion">
                            <RowAddEvent v-for="(item, index) of eventsaSeleccionar" :camposaMostrar= "camposaMostrar" :collectionName = "schema.definitions.event" :key="index" :item = "item" :index = "index" v-on:editar = "editar"/>
                        </template>
                    </tbody>
                </table>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import { mapState } from "vuex";

import Vue from 'vue'
import Row from "@/components/Row.vue"
import RowAddEvent from "@/components/RowAddEvent.vue"
import RowMinusEvent from "@/components/RowMinusEvent.vue"

export default {
    name: "ChannelEvents",
    components:{
        Row,
        RowAddEvent,
        RowMinusEvent
    },
    
    data () {
        return {
        }
    },
    props:[
      "item"
    ],
    computed: {
        ...mapState(["params", "datos", "schema"]),
        eventosdeCanal(){
            let events = [];
            if(this.params.registroenEdicion){
                for(let i = 0; i < this.params.registroenEdicion.events.length; i++){
                    for(let j = 0; j < this.datos.events.length; j++ ){
                        if(this.params.registroenEdicion.events[i].idEvent == this.datos.events[j].idEvent){
                            events.push(this.datos.events[j]);
                        }
                    }
                }
            }
            /*alert("this.params.registroenEdicion.pautas: " + JSON.stringify(this.params.registroenEdicion.pautas));
            alert("this.datos.pautas: " + JSON.stringify(this.datos.pautas));
            alert("pautas de Canal: " + JSON.stringify(pautas));*/
            return events;
        },
        eventsaSeleccionar(){
            let events = [];
            for(let i = 0; i < this.datos.events.length ; i++){
                var eventoSeleccionado = false;
                for(let j = 0; j < this.params.registroenEdicion.events.length; j++ ){
                    if(this.params.registroenEdicion.events[j].idEvent == this.datos.events[i].idEvent){
                        eventoSeleccionado = true;
                        break;
                    }
                }
                if(!eventoSeleccionado){
                    events.push(this.datos.events[i]);
                }
            }
            /*alert("this.params.registroenEdicion.pautas: " + JSON.stringify(this.params.registroenEdicion.pautas));
            alert("this.datos.pautas: " + JSON.stringify(this.datos.pautas));
            alert("pautas a seleccionar: " + JSON.stringify(pautas));*/
            return events;
        },
        properties(){
            //alert("schemaName: " + JSON.stringify(this.schemaName));
            return this.schema.definitions.event.properties;
        },
        camposaMostrar(){
            let campos = {};
            for(var i in this.properties){
                if(this.properties[i].mostrarenTabla){
                    campos[i] = this.properties[i];
                }
            }
            //alert("campos a mostrar: " + JSON.stringify(campos));
            return campos
        }
    },
    mounted(){
        //alert("en channelPautas: " + JSON.stringify(this.item));
        $('.carousel').carousel({
          interval: 2000
        })
    },
    updated(){
        //alert("en channelPautas: " + JSON.stringify(this.item));
        $('.carousel').carousel({
          interval: 2000
        })
    },
    methods:{
        editar(){
        },
        agregarEvento(){
            //alert("agregando evento");
        }
    }
}
</script>